.header-main {
  position: relative;
  z-index: 0;
  padding-bottom: 207px;

  .title {
    position: relative;
    z-index: 1;
    margin-bottom: 70px;

    svg {
      position: absolute;
      z-index: -1;
      top: -57.8%;
      left: -47%;
      width: 328px;
      user-select: none;
      fill: $blue1;
    }
  }

  //.header-main-row
  &__row {
    @include col();
    @include shift-left(3);
    @include size(6);
  }

  // .header-main__img
  &__img {
    position: absolute;
    top: -10px;
    right: -10%;
    user-select: none;
    pointer-events: none;
  }
}
