.header-deal {
  padding-bottom: 26px;

  .title {
    margin-bottom: 10px;
  }

  .btn {
    width: 370px;
    margin-top: 40px;
  }

  &__row {
    @include col();
    @include shift-left(3);
    @include size(9);

    p {
      font-size: 14px;
      margin-bottom: 1em;
    }

    span {
      font-size: 12px;
    }
  }
}
